body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#profile-picture{
    position: absolute;
    width: 40px; 
    height: 40px; 
    float: right !important;
    margin-left: 60%;
}

#user-name{
    position: absolute;
    font-size: 15px;
   margin-left: 50%;
    
}

#proffesion{
    position: absolute;
    font-size: 10px;
    margin-top: 20px;
   margin-left: 50%;
}

@media (max-width: 768px) {
    #proffesion, #user-name, #profile-picture{
        display: none;
    }
}



.container-fluid{
border-style: solid;
border-top: 0px;
border-left: 0px;
border-right: 0px;
border-color: #ddd;
}

#ocultar{
    display: none;
}


#Isotipo-nav {
    width: 4%;
    margin: 5px;
    margin-left: 40px;
}

#Logotipo-nav {
    width: 24%;
    margin: 10px;
    margin-left: 10px;
}

.navbar-toggler{
    border-style: none;
}

@media (max-width: 768px) {
    .navbar-brand{
        display: flex;
        justify-content: center;
    }
    #Logotipo-nav{
        margin: 12px;
    }

}



@media (max-width: 544px) {
    #proffesion, #user-name, #profile-picture{
        display: none;
    }

    #Isotipo-nav {
        width: 5%;
        margin: 5px;
        margin-left: 40px;
    }
    
    #Logotipo-nav {
        width: 40%;
        margin: 10px;
        margin-left: 10px;
    }

}

@media (max-width: 450px) {
    #Isotipo-nav {
        width: 12%;
        margin: 6px;
        margin-left: 40px;
    }
    
    #Logotipo-nav {
        width: 60%;
        margin: 15px;
        margin-left: 10px;
    }

    #ocultar{
        display: inline-block;
    }

}


#title-client{
    padding-top: 30px;
    font-size: larger;
    font-weight: bolder;
    color: #263247
}
#nit-info{
    color: #8292a9;
    margin: 5px;
}
#infoComplementary{
    font-weight: bolder;
    font-size: 12px;
    color: #1f2b41;
}

{/*@media (max-width: 768px) {*/}
@media (max-width: 450px) {
    #title-client{
        padding-top: 30px;
        font-size: 20px;
        font-weight: bolder;
        color: #263247
    }
}



.d-flex align-items-end flex-column{
    background-color: #fff;
}
#Logotipo-GrupoAVAL{
    width: 150px;
    margin: 30px;

}


@media (max-width: 768px) {
    #logo-GA{
        align-items: center !important;
    }

}
#contextMenu__item{
margin: 0px 20px 0px 20px;
width: 180px;
}

*{
    box-sizing: border-box;
}
#explanationContext{
  padding-left: 0px;
}
.d-flex {
  overflow: hidden;
  background-color: #fff;
}

.d-flex button {
  margin-left: 100px;
  float: left;
  display: block;
  color: black;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  border-bottom: 3px solid transparent;
}

.d-flex button:hover {
  border-bottom: 3px solid #158acd;
}

.d-flex button.active {
  border-bottom: 3px solid #158acd;
}

.unActive{
  background-color: #fff
}
/* TOP NAVV */

@media (max-width: 450px) {
  #contxtmnu{
    display: none !important;
  }
}
.d-flex {
  overflow: hidden;
  background-color: #fff !important;
}

a{
  text-decoration: none;
}

.explanation-text{
    color: #1b8dce;
    padding: 15px;
    margin-left: 30%;
}

button{
  border: none;
}

#continue-btn button:hover {
  text-decoration: overline !important;
}

.card{
  display: block;
  margin: 20px;
}

  #container-card{
    display: inline-block;
    margin: 20px 50px 50px 50px;
  }

  #change-bg{
    color: #000000 !important;
  }
  #continue-btn{
    background-color: #f7f7f7 !important;
    padding: 5% 0 5% 0;
  }

  #description{
    text-decoration:none; 
    padding-bottom: 10px;
  }
  #continuar-boton{
    color: #656565; 
    padding: 10px;
  }

  .options-icon{
    padding: 15% 20% 0 35%
  }

@media (max-width: 794px) {
  #continue-btn{
    background-color: #e8ecee !important;
    padding: 5% 34% 5% 40%;
  }
}
@media (max-width: 320px) {
  #continue-btn{
    background-color: #e8ecee !important;
    padding: 5% 37% 5% 40%;
  }
  #explanationContext{
    margin-left: 30px;
  }

  #container-card{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
  }

  #card-txt{
    font-size: 10px !important;
  }
}

.back{
    padding-top: 0px;
    padding-left: 60px;
    font-size: 50px;
    position: absolute;
    border-style: none;
    color: #1b8dce;
    background-color: #fff;
}

{/*@media (max-width: 768px) {*/}
@media (max-width: 450px) {
    .back{
        display: none;
    }
}
